@import "../../Scss/Variables.scss";
@import "../../Scss/Mixin.scss";
.course__detail {
  text-decoration: none !important;
  width: 24%;
  max-width: 300px;
  position: relative;
  margin: 15px 0;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.12);
  transition: 0.5s ease-in-out;
  border-radius: 5px;
  .courseDetail {
    border-radius: 0;
    z-index: 5;
    i {
      z-index: 3;
      position: absolute;
      top: 10px;
      right: -5px;
      font-size: 25px;
      color: $pink;
      transform: rotate(90deg);
    }
    span.icon {
      width: 10px;
      height: 10px;
      content: "";
      background-color: $black;
      display: block;
      position: absolute;
      top: 26px;
      right: -5px;
      z-index: -2;
      transform: rotate(45deg);
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      -o-transform: rotate(45deg);
    }
    .card__img {
      position: relative;
      border-radius: 0;
      z-index: 2;
      overflow: hidden;
      img {
        width: 100%;
        border-radius: 0;
        transition: all 3s;
        -webkit-transition: all 3s;
        -moz-transition: all 3s;
        -ms-transition: all 3s;
        -o-transition: all 3s;
      }
      .layer {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: transparent;
        border: 1px solid transparent;
      }
    }
    .card-body {
      padding: 20px 15px 15px 15px;
      h5 {
        @include text($fs-1 + 3, $fw-2, $black);
        transition: $eo;
      }
      .card-text {
        @include text($fs-1 - 2, $fw-2, $grey);
        margin-bottom: 0;
      }
      .kind-price {
        display: flex;
        justify-content: space-between;
        .kind {
          color: $black;
          font-style: italic;
          font-size: 1.6rem;
          color: #006151;
        }
      }
      .price {
        @include text($fs-1, $fs-3, $grey);
        margin-bottom: 0;
        transition: $eo;
      }
      .content__course {
        @include text($fs-1, $fw-3, $black);
        font-size: 1.6rem;
      }
    }
  }
  &:hover {
    .card-body {
      .card-title,
      .price {
        color: $pink;
      }
    }

    box-shadow: 0 1px 2px -2px rgba(0, 0, 0, 0.16),
      0 3px 6px 0 rgba(0, 0, 0, 0.12), 0 5px 12px 4px rgba(0, 0, 0, 0.09);
    .card__img {
      img {
        transform: scale(1.2);
        -webkit-transform: scale(1.2);
        -moz-transform: scale(1.2);
        -ms-transform: scale(1.2);
        -o-transform: scale(1.2);
      }
    }
  }
}
.render-button-details {
  padding: 0 15px 20px 15px;
  .content__course {
    font-size: 1.6rem;
  }
  .add__course {
    width: 100%;
    padding: 7px 0 !important;
    border: none;
    border-radius: 0;
    background-color: $pink;
    margin-bottom: 0 !important;
    color: $white;
    font-size: 1.6rem;
    &:before {
      background-color: $pink;
    }
    &:after {
      background-color: black;
    }
  }
  .goToCart {
    width: 100%;
    padding: 5px 0;
    border: 1px solid rgb(0, 115, 119);
    border-radius: 0;
    background-color: $white;
    color: rgb(0, 115, 119);
    font-size: 1.6rem;
    &:hover {
      color: $white;
      background-color: #006151;
    }
  }
  .go_details {
    margin-top: 5px;
    width: 100%;
    border: 1px solid rgb(0, 115, 119);
    border-radius: 0;
    background-color: $white;
    color: rgb(0, 115, 119);
    font-size: 1.6rem;
    padding: 5px 0;
    &:hover {
      color: $white;
      background-color: #006151;
    }
  }
}
@media screen and (max-width: 768px) {
  .courseList{
    .courseDetail{
      .list__course{
        .course-content{
          justify-content: space-around;
        }
      }
    }
  } 
  .course__detail {
    width: 49%;
  }
}
@media screen and (max-width: 576px) {
  .course__detail {
    width: 90%;
    margin: 20px auto;
  }
}
