@import "../../Scss/Variables.scss";
@import "../../Scss/Mixin.scss";
.login__form {
  .login {
    margin: $mg-3 0;
    text-transform: uppercase;
    color: $pink;
    font-size: 3rem;
  }
  label {
    @include text($fs-1, $fw-2, $pink);
  }
  input {
    border-radius: 0;
    font-size: 1.6rem;
    height: 40px;
    &:focus {
      border: 1px solid $black-1;
    }
  }
  .login__button {
    background-color: $black-1;
    border-radius: 0;
    color: $white;
    font-size: 1.6rem;
    height: 40px;
    &:focus {
      outline: none;
      box-shadow: none;
      background-color: $pink;
      border: 1px solid transparent;
    }
  }
}
.moveSignUp {
  text-align: center;
  margin-top: 15px;
  span {
    margin-right: 10px;
  }
  .btn-sign {
    color: $pink;
  }
}
